export default {
  SET_SESSION: (state, data) => {
    state.session = data;
  },
  SET_MENU: (state, data) => {
    state.menu = data;
  },

  SET_MENU_MAP: (state, data) => {
    state.menuMap = data;
  },

  SET_ROUTE: (state, data) => {
    state.route = data;
  },
}



